<template>
  <!-- App -->
  <div id="app">

    <!-- Statusbar -->
    <f7-statusbar></f7-statusbar>

    <!-- Main Views -->
    <f7-views>
      <f7-view id="main-view" navbar-through :dynamic-navbar="true" main>
        <!-- iOS Theme Navbar -->
        <f7-navbar v-if="$theme.ios">
          <f7-nav-center sliding>Jooku</f7-nav-center>
        </f7-navbar>
        <!-- Pages -->
        <f7-pages>
          <f7-page>
            <!-- Material Theme Navbar -->
            <f7-navbar v-if="$theme.material">
              <f7-nav-left>
                <f7-link icon="icon-bars" open-panel="left"></f7-link>
              </f7-nav-left>
              <f7-nav-center sliding>Jooku</f7-nav-center>
              <f7-nav-right>
                <f7-link icon="icon-bars" open-panel="right"></f7-link>
              </f7-nav-right>
            </f7-navbar>
            <!-- Page Content -->
            <f7-block-title>Create</f7-block-title>
            <f7-list>
              <f7-list-item link="/create" title="Create a new promotion."></f7-list-item>
            </f7-list>
            <f7-block-title>Pre-set Promotions</f7-block-title>
            <f7-list>
              <f7-list-item smart-select title="Promotion">
                <!-- Select with values inside -->
                <select name="promotion" @change="updatedPromotion($event)">
                  <option value="" :selected="[1, 2, 3].indexOf(promotion) === -1">Please select</option>
                  <option value="1" :selected="promotion === 1">25%</option>
                  <option value="2":selected="promotion === 2">50%</option>
                  <option value="3":selected="promotion === 3">75%</option>
                </select>
              </f7-list-item>
            </f7-list>
            <f7-block-title>Scan</f7-block-title>
            <f7-list>
              <f7-list-item link="/scan" title="Scan"></f7-list-item>
            </f7-list>
          </f7-page>
        </f7-pages>
      </f7-view>
    </f7-views>
  </div>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      promotion: null,
    }
  },
  created () {
    firebase.database().ref('promotion-new').on('value', (snapshot => {
      const val = snapshot.val() || ''
      if (typeof val !== 'object') {
        this.promotion = parseInt(val)
      } else {
        this.promotion === val
      }
    }))
  },
  methods: {
    updatedPromotion (e) {
      const newVal = e.target.value
      this.promotion = parseInt(newVal)
      firebase.database().ref('promotion-new').set(newVal)
    }
  }
}
</script>
<style>
.list-block .item-input {
  width: unset !important;
}
</style>


