<template>
  <f7-page>
    <f7-navbar title="Create Promotion" back-link="Back" sliding></f7-navbar>
    <f7-block-title>Select background image:</f7-block-title>
    <f7-list form>
      <!-- Enables Smart Select behavior by adding "smart-select" prop -->
      <f7-list-item smart-select title="Background">
        <!-- Select with values inside -->
        <select name="background" @change="selectBg($event)">
          <option v-for="idx in bgs" :value="'bg'+idx">Background {{idx}}</option>
        </select>
      </f7-list-item>
    </f7-list>
    <f7-block-title>Select type:</f7-block-title>
    <f7-list form>
      <f7-list-item radio name="type" value="department" title="Department" @click="handleClick($event, 'department')" :checked="type === 'department'"></f7-list-item>
      <f7-list-item radio name="type" value="item" title="Item" @click="handleClick($event, 'item')" :checked="type === 'item'"></f7-list-item>
    </f7-list>
    <f7-block-title>Select details:</f7-block-title>
    <f7-list form>
      <f7-list-item smart-select title="Department" v-show="type === 'department'">
        <!-- Select with values inside -->
        <select name="departments" @change="selectDept($event)">
          <option v-for="name in departments" :value="name" :selected="department === name">{{name}}</option>
        </select>
      </f7-list-item>
      <f7-list-item smart-select title="Item" v-show="type === 'item'">
        <!-- Select with values inside -->
        <select name="items" @change="selectItem($event)">
          <option v-for="name in items" :value="name" :selected="item === name">{{name}}</option>
        </select>
      </f7-list-item>
    </f7-list>
    <f7-block-title>{{pTitle}}</f7-block-title>
    <f7-list form>
      <f7-list-item>
        <f7-input type="text" placeholder="E.g. 25 " v-model="p"/>
      </f7-list-item>
    </f7-list>
    <f7-button color="green" fill @click="handleSubmit">Submit</f7-button>
    <f7-block-title>Preview:</f7-block-title>

    <div class="poster" :style="{ backgroundImage: 'url(/static/images/'+background+'.png)'}">
      <span class="saving">{{saving}}</span>
      <img class="icon" :src="'/static/images/'+icon+'.png'">
      <span class="icon-name">{{iconName}}</span>
    </div>
  </f7-page>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      bgs: [1, 2, 3, 4, 5, 6, 7],
      background: 'bg1',
      type: 'department',
      departments: ['Bakery', 'Beauty', 'Electronics', 'Footwear', 'Fruits', 'Gardening', 'Laundry', 'Pastries', 'Toys'],
      department: 'Bakery',
      items: ['Blazer', 'Blouse', 'Casual Dress', 'Coat', 'Cocktail Dress', 'Evening Dress', 'Jacket', 'Jumper', 'Maxi Dress', 'Men Underwear', 'Party Dress', 'Shirt', 'Skirt', 'Summer Dress', 'Sweater', 'Trousers', 'T Shirt', 'Women Formal Shirt', 'Women Shirt', 'Women Underwear'],
      item: 'Blazer',
      p: ''
    }
  },
  computed: {
    pTitle () {
      return this.type === 'department' ? 'Input Discount Percentage:' : 'Input Starting Price:'
    },
    saving () {
      return this.type === 'department' ? `SAVE UP TO ${this.p}%` : `From Â£${this.p}`
    },
    icon () {
      return this.iconName.replace(/ /g,'')
    },
    iconName () {
      return this.type === 'department' ? this.department : this.item
    }
  },
  methods: {
    selectBg (e) {
      this.background = e.target.value
    },
    selectDept (e) {
      this.department = e.target.value
    },
    selectItem (e) {
      this.item = e.target.value
    },
    handleClick (e, type) {
      this.type = type
    },
    handleSubmit () {
      const { background, type, department, item, p } = this
      if (!(background && type && (department || item) && p)) {
        this.$f7.addNotification({
          title: 'Error Message',
          titleRightText: 'now',
          subtitle: 'Please fill up all details.',
          closeTimeout: 3000,
        })
        return
      }
      firebase.database().ref('promotion-new').set({
        background,
        type,
        name: type === 'department' ? department : item,
        value: p
      }).then(() => {
        this.$f7.addNotification({
          title: 'Success Message',
          titleRightText: 'now',
          subtitle: 'New Promotion Set!',
          closeTimeout: 3000,
        })
        this.$router.back()
      }).catch(err => {
        this.$f7.addNotification({
          title: 'Error Message',
          titleRightText: 'now',
          subtitle: 'Something went wrong. Try again.',
          closeTimeout: 3000,
        })
      })
    }
  }
}
</script>

<style scoped>
.poster {
  width: 100vw;
  height: calc(100vw * 4535 / 2551);
  background-size: 100%;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding-top: 10vw;

  color: red;
  font-family: PT Sans;
  font-size: 12vw;
  font-weight: bold;
}

.icon {
  width: calc(100vw * 4535 / 2551 * 0.4);
  height: calc(100vw * 4535 / 2551 * 0.4);
  margin: 5vw 0;
}
</style>


