<template>
  <f7-page>
    <f7-navbar title="Scan" back-link="Back" sliding></f7-navbar>
    <div v-show="!isScanning">
      <h3>{{msg}}</h3>
      <p><a href="#" class="button" @click="isScanning = true">Scan again</a></p>
    </div>
    <qrcode-reader v-if="isScanning" class="qrcode-reader" @decode="onDecode"></qrcode-reader>
  </f7-page>
</template>

<script>
export default {
  data () {
    return {
      msg: '',
      isScanning: true
    }
  },
  methods: {
    onDecode (content) {
      this.isScanning = false
      this.msg = `Updating...`
      let promotion = parseInt(content)
      if ([1,2,3].indexOf(promotion) === -1) {
        this.msg = 'Error: Invalid Promotion QRCode.'
        return
      }
      firebase.database().ref('promotion-new').set(promotion)
        .then(() => {
          this.msg = 'Success: Promotion Updated.'
        })
        .catch(err => {
          this.msg = 'Error: Something went wrong. Try again later.'
        })
    }
  }
}
</script>
<style scoped>
.list-block {
  display: flex;
  justify-content: center;
  align-items: center;
}
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
iframe {
  margin: 0 20px;
  margin-top: 20px;
  height: 40px;
}
</style>


